//CLASE AUTOPLAY
//Se crea la clase AutoPlay la cual es un plugin para generar una reproducciÃ³n automÃ¡tica al cargar el sito web
export default class AutoPlay{
    constructor(){ 
        
    }
     //Enviamos un objeto "player" que contiene las propiedades y mÃ©todos de la clase MediaPlayer
    run(player:MediaPlayer){
        //Auto play busca que el video estÃ© en muted para poderlo reproducir
        !player.video.muted ? player.video.muted = true : false//si el video no estÃ¡ muted, se establece en muted
        player.playVideo();
    }
} 