class Ads{
    private static instance: Ads;
    private ads: Ad[];
    private constructor(){
        this.initAds();
    }
    static getInstance(){
        if(!Ads.instance){
            Ads.instance = new Ads();
        }
        return Ads.instance;
    }
    private initAds(){
      this.ads = [...ALL_ADS];
    }
    //Este mÃ©todo obtiene los ads 1 a 1.
    getAds(){
      if(this.ads.length === 0){
        this.initAds();
      }
      return this.ads.pop();
    }
}

export default Ads;