import MediaPlayer from "../MediaPlayer";//Importamos la clase para que exista este tipo de dato en el archivo
//y asÃ­ poder tipar con Ã©l.

export default class AutoPause{
    private video:HTMLMediaElement;
    constructor(video){
        
        this.video = video;
    }
  
    run(player:MediaPlayer){
        //Definimos el objeto que llevarÃ¡ las opciones o configuraciÃ³n del observador
        const ratio = {
            threshold : 0.25,
        }
        //Luego creamos una instancia de la clase InstersectionObserver
        //En este caso la funciÃ±on callback es definidad directamente como parÃ¡metro
        const observer = new IntersectionObserver((entries:IntersectionObserverEntry[]) =>{
            //Extraemos la entrada en la primera posiciÃ³n(ya que sÃ³lo hay una)
            const entry= entries[0];
            //Se compara si la cantidad de piixeles visibles del video es mayor menor a su 25%
            const isVisible = entry.intersectionRatio >= ratio.threshold;
            //Si es mayor, harÃ¡ play; si menor, harÃ¡ pause.
            if(isVisible){
                player.playVideo();
            }
            else{
                player.pauseVideo(); 
            }
            
        }, ratio/*AquÃ­ va el objeto de configuraciÃ³n */);
        
        //Observamos la etiqueta video de la pÃ¡gina
        observer.observe(this.video);

        document.addEventListener("visibilitychange", ()=>{
            //Si el estado es visible harÃ¡ play al video, de lo contrario serÃ¡ pause
            if (document.visibilityState === "visible")
                player.playVideo();
            else
                player.pauseVideo();
        })
        
    }
}